@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Tinos);
@import url(https://fonts.googleapis.com/css?family=Tinos);
@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
:root {
  --container-bg-color: #333;
  --left-bg-color: rgba(204, 0, 0, 0.7);
  --left-button-hover-color: rgba(255, 102, 102, 0.3);
  --middle-bg-color: rgba(43, 43, 43, 0.8);
  --middle-button-hover-color: rgba(92, 92, 92, 0.3);
  --right-bg-color: rgba(0, 76, 153, 0.8);
  --right-button-hover-color: rgba(51, 153, 255, 0.3);
  --hover-width: 50%;
  --other-width: 25%;
  --other-other-width: 25%;
  --speed: 1000ms;
}

/* Global */

html, body, #root {
  padding:0;
  margin:0;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
}

a:link{
  text-decoration: none!important;
}

.split.left .button:hover {
  background-color: rgba(255, 102, 102, 0.3);
  background-color: var(--left-button-hover-color);
  border-color: rgba(255, 102, 102, 0.3);
  border-color: var(--left-button-hover-color);
}

.split.middle .button:hover {
  background-color: rgba(92, 92, 92, 0.3);
  background-color: var(--middle-button-hover-color);
  border-color: rgba(92, 92, 92, 0.3);
  border-color: var(--middle-button-hover-color);
}

.split.right .button:hover {
  background-color: rgba(51, 153, 255, 0.3);
  background-color: var(--right-button-hover-color);
  border-color: rgba(51, 153, 255, 0.3);
  border-color: var(--right-button-hover-color);
}

/* Big screen */

@media(min-width: 801px) {

  h1.landing {
    font-size: 2.5rem;
    color: #fff;
    position: absolute;
    left: 50%;
    top: 20%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    white-space: nowrap;
  }

  .button {
    display: block;
    position: absolute;
    left: 50%;
    top: 40%;
    height: 2.5rem;
    padding-top: 1.3rem;
    width: 15rem;
    text-align: center;
    color: #fff;
    border: #fff solid 0.2rem;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }

  .container {
    position: relative;
    width: 100%;
    height: 100%;
    background: #333;
    background: var(--container-bg-color);
    font-family: 'Roboto', sans-serif;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  }

  .split {
    position: absolute;
    width: 50%;
    height: 100%;
    overflow: hidden;
  }

  .split.left {
    width: 25%;
  }

  .split.right {
    width: 25%;
  }

  .split.left {
    left:0;
    background: url(/static/media/book.95be5582.jpeg) center center no-repeat;
    background-size: cover;
    z-index: 2;
  }

  .split.left:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(204, 0, 0, 0.7);
    background: var(--left-bg-color);
  }

  .split.middle {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: url(/static/media/code.7bd1e383.png) center center no-repeat;
    background-size: cover;
    z-index: 1;
  }

  .split.middle:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(43, 43, 43, 0.8);
    background: var(--middle-bg-color);
  }

  .split.right {
    right:0;
    background: url(/static/media/movie.3f462af1.jpg) center center no-repeat;
    background-size: cover;
    z-index: 2;
  }

  .split.right:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(0, 76, 153, 0.8);
    background: var(--right-bg-color);
  }

  .split.left, .split.right, .split.middle:before, .split.middle, .split.right:before, .split.left:before {
    -webkit-transition: 1000ms all ease-in-out;
    transition: 1000ms all ease-in-out;
    -webkit-transition: var(--speed) all ease-in-out;
    transition: var(--speed) all ease-in-out;
  }

  .hover-left .left {
    width: 50%;
    width: var(--hover-width);
  }

  .hover-left .middle {
    width: 50%;
    width: var(--hover-width);
  }

  .hover-left .right {
    width: 25%;
    width: var(--other-width);
  }

  .hover-left .middle:before .right:before {
    z-index: 20;
  }

  .hover-middle .middle {
    width: 50%;
  }

  .hover-middle .left {
    width: 25%;
  }

  .hover-middle .right {
    width: 25%;
  }

  .hover-middle .left:before .right:before {
    z-index: 20;
  }

  .hover-right .right {
    width: 50%;
    width: var(--hover-width);
  }

  .hover-right .middle {
    width: 50%;
    width: var(--hover-width);
  }

  .hover-right .left {
    width: 25%;
    width: var(--other-width);
  }

  .hover-right .left:before .middle:before {
    z-index: 20;
  }

}

/* Small Screen */

@media(max-width: 800px) {
  h1.landing {
    font-size: 1.5rem;
    color: #fff;
    z-index: 9001;
    white-space: nowrap;
    color: #fff;
    border: #fff solid 0.2rem;
    padding: 0.7rem;
  }

  .button {
    display: none;
  }

  .container {
    position: relative;
    width: 100%;
    height: 100%;
    background: #333;
    background: var(--container-bg-color);
    font-family: 'Roboto', sans-serif;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  }

  .split {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 33%;
    overflow: hidden;
  }

  .split.left {
    background: url(/static/media/book.95be5582.jpeg) center center no-repeat;
    background-size: cover;
  }


  .split.left:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 33%;
    background: rgba(204, 0, 0, 0.7);
    background: var(--left-bg-color);
  }


  .split.middle {
    background: url(/static/media/code.7bd1e383.png) center center no-repeat;
    background-size: cover;
    height: 34%;
  }


  .split.middle:before {
    position:absolute; 
    content: "";
    width: 100%;
    height: 34%;
    background: rgba(43, 43, 43, 0.8);
    background: var(--middle-bg-color);
  }


  .split.right {
    background: url(/static/media/movie.3f462af1.jpg) center center no-repeat;
    background-size: cover;
  }

  .split.right:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 33%;
    background: rgba(0, 76, 153, 0.8);
    background: var(--right-bg-color);
  }


}

nav ul {
