}

.nav-container {
  max-width: 1000px;
  margin: 0 auto;
}

/* BIG SCREEN */

@media screen and (min-width: 800px) {
  nav {
    float: right;
  }
  .nav-list {
    display: block !important;
  }
  .navigation {
    height: 70px;
    background: black;
  }
}


/* SMALL SCREEN */

@media only screen and (max-width: 800px) {

  .nav-mobile {
    display: inline;
  }

  .navigation {
    background: black;
    text-align: left;
  }


  nav {
    width: 100%;
    padding: 70px 0 0px;
  }

  nav .nav-list {
    display: none;
  }

  nav .nav-list.active {
    display: inline;
  }

  nav ul li {
    float: none;
  }
  nav ul li a {
    padding: 15px;
    line-height: 20px;
  }
  nav ul li ul li a {
    padding-left: 30px;
  }

  #nav-toggle {
    position: absolute;
    left: 18px;
    top: 22px;
    cursor: pointer;
    padding: 10px 35px 16px 0px;
  }
  #nav-toggle span,
  #nav-toggle span:before,
  #nav-toggle span:after {
    cursor: pointer;
    border-radius: 1px;
    height: 5px;
    width: 35px;
    background: #463f3f;
    position: absolute;
    display: block;
    content: '';
  }
  #nav-toggle span:before {
    top: -10px;
  }
  #nav-toggle span:after {
    bottom: -10px;
  }
}



.at-banner {
  z-index: 999999;
  position: fixed;
  bottom: -1px;
  right: 0;
  left: 0;
  background: #974DF3;
  width: 100%;
  border-bottom: 1px solid #EEEEEE;
  padding: 10px;
  box-sizing: border-box;
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%);
  color: #FFFFFF;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-animation: at-banner-slide-in 1.3s ease forwards;
          animation: at-banner-slide-in 1.3s ease forwards;
}

.at-banner__content {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 90%;
  margin: 0 auto;
  padding: 10px 40px 10px 40px;
  box-sizing: border-box;
}

.at-banner__title {
  font-size: 18px;
}

.at-banner__text {
  margin: 0 20px 0 0;
}

.at-banner__button {
  display: inline-block;
  background: #FFFFFF;
  height: 40px;
  border: 0;
  border-radius: 2px;
  box-shadow: 0 2px 4px rgba(#000, 0.1);
  padding: 0 20px;
  color: #974DF3;
  font-size: 12px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  white-space: nowrap;
}

.at-banner__close {
  position: absolute;
  top: 50%;
  right: 20px;
  width: 20px;
  height: 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
}

.at-banner__close:before,
.at-banner__close:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 3px;
  border-radius: 2px;
  -webkit-transform-origin: center;
          transform-origin: center;
}

.at-banner__close:before {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
}

.at-banner__close:after {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
}


@-webkit-keyframes at-banner-slide-in {
  0% {
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
  }

  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}


@keyframes at-banner-slide-in {
  0% {
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
  }

  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@media only screen and (max-width: 800px) {
  .at-banner__text {
    margin: 0 5px 0 0;
  }  
  .at-banner__content {
    padding: 10px 10px 10px 0px;
    margin-left: 0;
  }
}


@-webkit-keyframes at-banner-slide-out {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
  }
}


@keyframes at-banner-slide-out {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
  }
}


