/* global */

html, body {
  height: 100%;
  background: #fcfcfa;
}
.hidden {
  display: none;
}
.invisible {
  visibility: hidden;
  opacity: 0;
  height: 0;
  -webkit-transition-property: opacity 0;
  -webkit-transition-duration: 0s;
  -webkit-transition-timing-function: ease-out;
  transition-property: opacity 0;
  transition-duration: 0s;
  transition-timing-function: ease-out;
  position: absolute;
}
.yearProjects.active {
  visibility: visible; /* shows sub-menu */
  height: auto;
  opacity: 1;
  -webkit-transition-duration: 1s;
  transition-duration: 1s;
}
img.appIcon {
  height: 30px;
  width: 30px;
  padding-right: 2px;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 80px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 2px 0;
  position: absolute;
  z-index: 1;
  bottom: 110%;
  left: 50%;
  margin-left: -40px;
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}


