/* Full Screen Overlay Navigation - W3Schools.com */

.overlay {
  height: 0%; 
  width: 100%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0, 0.9);
  overflow-y: auto;
  transition: 0.5s;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}

@media screen and (max-height: 600px) {
  .overlay {overflow-y: auto;}
  .overlay .closebtn {font-size: 20px}
  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

.overlay .closebtn {
  padding: 8px;
  text-decoration: none;
  font-size: 60px;
  color: #818181;
  display: block;
  transition: 0.3s;
}
.overlay .closebtn:hover, .overlay .closebtn:focus {
  color: #f1f1f1;
}

/* Image Hover, @littlesnippets.net CodePen.io */

@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);

.snip1556 {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  color: #000000;
  display: inline-block;
  font-family: 'PT Sans', 'Oswald', Arial, sans-serif;
  font-size: 16px;
  margin: 8px;
  max-width: 310px;
  min-width: 250px;
  position: relative;
  text-align: left;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: transparent;
  box-shadow: none !important;
  padding: 30px 40px 0 0;
}
.snip1556 *,
.snip1556 *:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
}
.snip1556 img {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  max-width: 100%;
  position: relative;
  vertical-align: top;
}
.snip1556 figcaption {
  background-color: #111;
  bottom: 15px;
  left: 20px;
  position: absolute;
  right: 0;
  top: 0;
}
.snip1556 h3 {
  font-size: 26px;
  font-weight: 300;
  position: absolute;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  -webkit-transform-origin: 0% 0%;
  -ms-transform-origin: 0% 0%;
  transform-origin: 0% 0%;
  color: #fff;
  left: 100%;
  letter-spacing: 1px;
  margin: 12px 0;
  opacity: 0.8;
  padding: 6px 40px 0 0;
  right: 0;
  text-transform: uppercase;
  top: 0;
  white-space: nowrap;
}
.snip1556 a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.snip1556:hover figcaption,
.snip1556.hover figcaption {
  background-color: #c0392b;
}

/* Personal */
.reviewsRoot {
  background-color: #232B2B;
  color: white;
  min-height: 100%;
  text-align: center;
}

.reviewsContainer {
  width: 70%;
  text-align: center;
  margin: auto;
}

#overlayPic {
  margin-bottom: 10px;
  text-align: center;
}

#actualContent {
  font-family: georgia,'times new roman',times,serif;
  font-size: 1.125rem;
  color: #ccc;
  line-height: 1.5625rem;
  white-space: pre-line;
}

.overlay-content {
  position: relative;
  width: 70%;
  margin-top: 90px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.float{
  position:fixed;
  width:60px;
  height:60px;
  bottom:40px;
  right:40px;
  background-color:#0C9;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  box-shadow:1px 1px 2px #999;
}

.my-float{
  position:fixed;
  bottom:60px;
  right:64px;
}

.reviewImage {
  max-height: 300px;
}

@media screen and (max-width: 600px) {
  .reviewsContainer {
    width: 96%;
  }
  .overlay-content {
    width: 96%;
  }
  .reviewImage {
    max-height: 200px;
  }
  #actualContent {
    line-height: 24px;
  }
  .overlay .closebtn {
    top: 0px;
    right: 20px;
  }
  .overlay-content {
    margin-top: 70px;
  }
  .snip1556 h3{
    display: none; 
  }
  .snip1556 {
    min-width: 150px;
    max-width: 250px;
    padding: 10px 10px 0 0;
  }
  .snip1556 figcaption {
    display: none;
  }
  
}
